export default function Notifications() {
  const notifications = [
    { id: 1, message: "New fuel order request from Attendant John", severity: "Info" },
    { id: 2, message: "Pending invoice approval", severity: "High" },
  ]

  return (
    <div>
      <h1 className="text-2xl font-bold mb-4">Notifications</h1>
      <ul className="space-y-3">
        {notifications.map((n) => (
          <li
            key={n.id}
            className={`p-4 rounded-xl shadow-md ${
              n.severity === "High" ? "bg-red-100" : "bg-blue-100"
            }`}
            aria-live="polite"
          >
            <strong>{n.message}</strong> â€” <em>{n.severity}</em>
          </li>
        ))}
      </ul>
    </div>
  )
}
